import * as React from 'react';
import styled from 'styled-components';
import { SocialIcon } from 'react-social-icons';

export interface INameProps {
}

const H1 = styled.h1`
  margin-right: 20px;
    color:black;
    font-size:4rem;
    text-align:center;
    @media (max-width: 768px) {
        font-size:3rem;
    }
    @media (max-width: 400px) {
        font-size:2rem;
    }
`;

const Div = styled.div`
    height: 20vh;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    background: linear-gradient(45deg, #ffffff,#cefff8 );
    @media (max-width: 768px) {
      flex-direction: column;
    }

`;

const Iconwrap = styled.div`
    display:flex;
    justify-content: space-around;
    align-items: center;

    width: 10vw;
    @media (max-width: 768px) {
      width:60vw;
    }
`

export default function Name (props: INameProps) {
  return (
    <Div>
      <H1>I'm Maiko Cheng </H1>
      <Iconwrap>
        <SocialIcon url="https://www.linkedin.com/in/maiko-cheng-461474168/" network="linkedin"/>
        <SocialIcon url="https://www.instagram.com/_maiko_c_/?hl=en" network="instagram"/>
        <SocialIcon url="https://github.com/MaikoCheng" network="github"/>
      </Iconwrap>
    </Div>
  );
}
