
import * as React from 'react';
import { useState } from 'react';
import styled,{ keyframes } from 'styled-components';
import chest from './videos/Chest.gif'
import chestclosed from './videos/chestclosed.gif';
import dj from './videos/dj.png'
import social from './videos/social.png'
import socials from './videos/socials.png'


export interface IAboutProps {
    displaya:boolean;
}

const move = keyframes`
    0% {
        transform: translateX(-5vw);
    }
    100% {
        transform: translateX(0vw);
    } 
`;


const OutsideWrapper = styled.div`
    /*margin-top: 150px;*/
    padding-top: 50px;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    position: relative;
    overflow: hidden;
    margin-bottom: 20px;
    background-color: #000000;
    background: linear-gradient(45deg, #000000, #09032a);
`;

const Chestdiv = styled.div`
    width: 100%;

    @media (max-width: 768px) {
        width: 50vw;
    }
`;

const Chest = styled.img`
    width: 100%;
    display:${(p: IAboutProps) => p.displaya ? 'block': 'none'};
`;

const Chestclosed = styled.img`
    width: 100%;
    cursor: pointer;
    display: ${(p: IAboutProps) => p.displaya ? 'none': 'block'};
    
    
`;

const SubText = styled.h2`
  align-self: center;
  color:white;
  font-size: 3rem;
  font-weight: bold;
  margin: 0;
  display:${(p: IAboutProps) => p.displaya ? 'none': 'block'};
  

`;

const ChestDivWrapper = styled.div`
    width: 300px;
    @media (max-width: 768px) {
        width: 50vw;
        margin-top: 100px;
    }
`;

const Abouttext = styled.h1`
    color: #ffffff;
    font-size: 3rem;
    
`;

const Aboutdiv = styled.div`

    width: 100%;
    display: ${(p: IAboutProps) => p.displaya ? 'flex': 'none'};
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align:center;
    height:30vh;
    animation: ${move} 2s ease-in-out;
`;

const Blue = styled.a`
    color: #25377D;
    text-align:center;
    text-decoration: none;
`;
const Red = styled.a`
    color: #CD2C5C;
    text-align:center;
    text-decoration: none;
`;

const Djpic = styled.img`
    width: 350px;
    border-radius: 10px;

    margin-top: 50px;
    display: ${(p: IAboutProps) => p.displaya ? 'flex': 'none'};
`;

const Socialpic = styled(Djpic)`
    @media (max-width: 1050px) {
            display:none;
        }
        
`;
const Photos = styled.div`
    width: 100%;
    display: flex;
    flex-direction:row; 
    justify-content: space-around; 
    flex-wrap: wrap;
    @media (max-width: 768px) {
        display:none;
    }
    animation: ${move} 2s ease-in-out;
    
`;
export default function About (props: IAboutProps) {

  const [display, setDisplay] = useState(false);

  function handleclick(){
    setDisplay(true);
    
    }
  return (
    <OutsideWrapper>
        
        <Aboutdiv displaya={display}>
            <Abouttext>I am a second year student at <Blue href={'https://www.tue.nl/en/'} target="_blank">TU</Blue><Red href={'https://www.tue.nl/en/'} target="_blank">/</Red><Blue href={'https://www.tue.nl/en/'} target="_blank">e</Blue>
            </Abouttext>
            <Abouttext>myLife = &#123; music, games, social activities &#125;</Abouttext>
            
        </Aboutdiv>
          
    <ChestDivWrapper>
      <Chestdiv onClick={handleclick}>
          <Chest displaya={display} src={chest} alt="Chest"/>
          
          
        <Chestclosed displaya={display} src={chestclosed} alt="Chest"/>
          
          

      </Chestdiv>
    </ChestDivWrapper>
        <SubText displaya={display}>Click me!</SubText>
        
        <Photos>
        <Djpic displaya={display} src={dj} alt="Dj"/>
        <Djpic displaya={display} src={social} alt="Dj"/>
        <Socialpic displaya={display} src={socials} alt="Dj"/>

        </Photos>
    </OutsideWrapper>
  );
}
