import * as React from 'react';
import {FaReact, FaCss3Alt,FaHtml5,FaJsSquare,FaAngular,FaJava,FaFigma,FaPython} from 'react-icons/fa'
import styled,{ keyframes } from 'styled-components'


export interface ICarouselpartProps {
}
const move = keyframes`
    0% {
        transform: translateX(0vw);
        visibility: visible;
    }
    100% {
        transform: translateX(90vw);
    } 
`;

const Logo1 = styled.div`
    height: 100px;
    visibility:hidden;
    animation-name: ${move};
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    position: absolute;
    animation-delay: 0s;
    left:0px;

    @media (max-width: 768px) {
      height:50px;
      width:50px;
    }
    

`;
const Logo2 = styled.div`
    height: 100px;
    visibility:hidden;
    animation-name: ${move};
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    position: absolute;
    animation-delay: 1.286s;
    left: 0px;

`;

const Logo3 = styled.div`
    height: 100px;
    visibility:hidden;
    animation-name: ${move};
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    position: absolute;
    animation-delay: 2.571s;
  


`;
const Logo4 = styled.div`
    height: 100px;
    visibility:hidden;
    animation-name: ${move};
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    position: absolute;
    animation-delay: 3.857s;
  

`;
const Logo5 = styled.div`
    height: 100px;
    visibility:hidden;
    animation-name: ${move};
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    position: absolute;
    animation-delay: 5.143s;
  

`;
const Logo6 = styled.div`
    height: 100px;
    visibility:hidden;
    animation-name: ${move};
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    position: absolute;
    animation-delay: 6.429s;
  

`;
const Logo7 = styled.div`
    height: 100px;
    visibility:hidden;
    animation-name: ${move};
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    position: absolute;
    animation-delay: 7.714s;
  

`;
const Logo8 = styled.div`
    height: 100px;
    visibility:hidden;
    animation-name: ${move};
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    position: absolute;
    animation-delay: 8.9s;
  


`;

const CarWrapper = styled.div`
    font-size:5rem;
    overflow: hidden;
    @media (max-width: 768px) {
      font-size:3rem;
    }
    max-width: 100vw;
`;

const CarWrapperWrapper = styled.div`
    position: relative;
    padding-bottom:20px;

`;

const Title = styled.h1`
    font-size: 4rem;
    color: #000;
    text-align: center;
`;

export default function Carouselpart (props: ICarouselpartProps) {
  return (
    <CarWrapperWrapper>
      <Title>My toolbox is constantly expanding</Title>
      <CarWrapper>
          <Logo1>
            <FaReact/>
          </Logo1>
          <Logo2>
            <FaCss3Alt/>
          </Logo2>
          <Logo3>
            <FaJava/>
          </Logo3>
          <Logo4>
            <FaFigma/>
          </Logo4>
          <Logo5>
            <FaHtml5/>
          </Logo5>
          <Logo6>
            <FaJsSquare/>
          </Logo6>
          <Logo7>
            <FaPython/>
          </Logo7>
          <Logo8>
            <FaAngular/>
          </Logo8>
      </CarWrapper>
    </CarWrapperWrapper>
      

  );
}
