import * as React from 'react';
import { useState,useEffect } from 'react';
import styled from 'styled-components';
import {AiOutlineMenu} from 'react-icons/ai';
import {IconContext} from 'react-icons';
import {animateScroll as scroll} from 'react-scroll'

const Top = styled.div`

  background-color: ${ (p: IHeadProps) => p.scrolled ?   '#000000':'transparent'};
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px;
  box-sizing: border-box;
  font-weight: bold;
  font-size: 1.5rem;
  font-family: "Sora";
  top:0px;
  margin-top: -50px;
  position: sticky;
  z-index: 100;
`;

const Navlogo2 = styled.div`
    color: white;
    @media (max-width: 768px) {
    display: none;
  }
`;
const Navlogo = styled.div`
    color: white;
    cursor: pointer;
`;

const Clapbar = styled.div`
  display: none;
  @media (max-width: 768px) {
    display: flex;

    justify-content: center;
    align-items: center;
    cursor: pointer;
    width:50px;
    height:100%;
  }
`;

const NavRef = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width:70px;
  height:100%;
  color:white;
  padding: 0 20px;
  margin: 0 5px;
  &:hover {
  border-bottom: 2px solid #38ffee;
  }
  @media (max-width: 768px) {
    display: none;
  }

`;



const Middlewrap = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width:100%;
  height:100%;
  background-color: transparent;
`;
export interface IHeadProps {
  scrolled: boolean;
}


export default function Head (props: IHeadProps) {
  const [HasScrolled, setHasScrolled] = useState(false);
  const handleScroll = () => {
    if(window.scrollY >= 50){
      setHasScrolled(true);

    }else{
      setHasScrolled(false);
    }
  };
  useEffect(() => {
    window.addEventListener('scroll',handleScroll); 
  },[]);
  return (
    <IconContext.Provider value={{ color: "white" }}>
      <Top scrolled={HasScrolled}>
          <Navlogo onClick={scroll.scrollToTop}>Maiko</Navlogo>
          <Middlewrap>
            <NavRef>About</NavRef>
            <NavRef>Skills</NavRef>
            <NavRef>Projects</NavRef>
            <NavRef>Contact</NavRef>
          </Middlewrap>
          <Clapbar><AiOutlineMenu/></Clapbar>
          <Navlogo2>Bravo</Navlogo2>
      </Top>
    </IconContext.Provider>

  );
}