import * as React from 'react';
import styled from 'styled-components';
import Video from './videos/video2.mp4';


export interface IHeroProps {
}

const HeroVideoDiv = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    overflow: hidden;
    right: 0;
    bottom: 0;
`;
const HeroContainer = styled.div`
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    overflow: hidden;
    
`;

const VideoBg = styled.video`
  width: 100%;
  height: 100%;
  --o-object-fit: cover;
  object-fit: cover;

  background: #232a34;
  -o-filter: blur(5px);
  filter: brightness(0.3);
`;

const MainTextWrap = styled.div`
  margin: 0 20vw;
  width:auto;
  height:30%;
  display:flex;
  flex-shrink: 2;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  z-index:1;
`;

const MainText = styled.h1`
  color:white;
  font-size: 5rem;
  text-align:center;
  margin: 0;

  
`;

const SubText = styled.h2`
  color:white;
  font-size: 3.7em;
  font-weight: normal;
  margin: 0;
  font-style: italic;
  text-align:center;

`;

export default function Hero (props: IHeroProps) {
  return (
    <HeroContainer>
      <HeroVideoDiv>
          <VideoBg autoPlay muted loop src={Video}/>
      </HeroVideoDiv>
      <MainTextWrap>
        <MainText>I like building websites</MainText>
        <SubText>That's probably why you're here</SubText>
      </MainTextWrap>
    </HeroContainer>
  );
}
