import * as React from 'react';
import styled from 'styled-components';
import drink from './videos/Drink.png';
import gelol from './videos/gelol.png';
import fluid from './videos/fluid.png';

export interface IProjectsProps {
}

const Wrapper = styled.div`
    display: flex;
    height:102vh;
    margin-top: 100px;
    padding: 0px;
    background: linear-gradient(45deg, black, #29032a);
    justify-content: center;
    align-items: center;
    flex-direction: column;
`
const Middlewrap = styled.div`
    display:flex;
    width:60%;
    height:80%;

    flex-direction: column;
`;

const Project = styled.div`
  background: url(${drink});
  background-repeat: no-repeat;
  background-size: cover; 
  width: 100%;
  background-position: center;
  height:30%;
  margin-top:15px;
  margin-bottom: 15px; 
  cursor:pointer;
  border-radius: 15px;
  transition: all 0.3s ease;
  &:hover{
    transform: scale(1.1);
  }
`;

const Project2 = styled(Project)`
  background: url(${gelol});
  background-repeat: no-repeat;
  background-size: cover; 
  width: 100%;
  background-position: center;
`;

const Project3 = styled(Project)`
  background: url(${fluid});
  background-repeat: no-repeat;
  background-size: stretch; 
  width: 100%;
  background-position: 50% 60%;
`;
const MainText = styled.h1`
  color:white;
  font-size: 4.6rem;
  text-align:center;
  margin: 0;
  margin-bottom: 20px;

  
`;

export default function Projects (props: IProjectsProps) {
  const gotourl = () => {
    window.open("https://maikos-stunning-project.webflow.io/",'_blank');
  }
  const gotourl2 = () => {
    window.open("https://maikocheng.github.io/GELOLDrinkingGame/index.html",'_blank');
  }
  const gotourl3 = () => {
    window.open("https://maikocheng.github.io/MySip/",'_blank');
  }

  return (
    <Wrapper>
      <MainText>My Projects</MainText>
      <Middlewrap>
        <Project onClick={gotourl}></Project>
        <Project2 onClick={gotourl2}></Project2>
        <Project3 onClick={gotourl3}></Project3>
      </Middlewrap>
    </Wrapper>
  );
}
