import * as React from 'react';
import styled from 'styled-components';

export interface IContactProps {
}

const Wrapper = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 30vh;
    background: linear-gradient(45deg, #ffffff,#cefff8 );
    flex-wrap: wrap;
`

const Contactdiv = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 40%;
    height: 80%;
    margin: 10px;

    
`
const Link = styled.h2`
    font-size: 1.5rem;
    cursor: pointer;
    @media (max-width: 768px) {
      font-size:0.9rem;
    }
    flex-wrap: wrap;
    

`
const Header = styled.h1`
    font-size: 3rem;
    @media (max-width: 768px) {
      font-size:2rem;
    }

`

const Whatsapp = styled.span`
    color: #25d366;
`

const Mail = styled(Whatsapp)`
    color: #0078D4;
`
export default function Contact (props: IContactProps) {
  const handleClick = () =>{
    window.open("mailto:maikocheng@hotmail.com");
  }

  const handlePhoneClick = () =>{
    window.open("tel:+32468120812");
  }
  return (
    <Wrapper>
      <Contactdiv>
        <Header>Send me an <Mail>email</Mail> 📧</Header>
        <Link onClick={handleClick}>maikocheng@hotmail.com</Link>
      </Contactdiv>
      <Contactdiv>
        <Header>Send me a <Whatsapp>Whatsapp</Whatsapp>📱</Header>
        <Link onClick={handlePhoneClick}>+32 468 12 08 12</Link>
      </Contactdiv>
    </Wrapper>
  );
}
